  align-items: center;
  display: flex;
}

.input__search {
  max-width: 490px;
  min-width: 350px;
  margin-right: 4px;
}

.input__search input {
  letter-spacing: -.06em;
  color: rgba(34, 43, 64, .5);
  width: 100%;
  height: 46px;
  background: rgba(34, 43, 64, .05);
  border: 1px solid #f3f3f3;
  border-radius: 23px;
  padding: 16px 24px;
  font-size: 14px;
  font-weight: 500;
  line-height: 100%;
}

.input__select {
  display: flex;
}

.__select {
  width: 190px;
  height: 46px;
  z-index: 1;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.__select[data-state="active"] .__select__title:before {
  transform: translate(-3px, -50%)rotate(-45deg);
}

.__select[data-state="active"] .__select__title:after {
  transform: translate(3px, -50%)rotate(45deg);
}

.__select[data-state="active"] .__select__content {
  border-width: 1px;
  border-color: #c7ccd1;
}

.__select[data-state="active"] .__select__label {
  max-height: 40px;
  border-width: 1px;
}

.__select[data-state="active"] .__select__label:before {
  opacity: 1;
}

.__select__title {
  width: 100%;
  height: 46px;
  letter-spacing: -.06em;
  color: #222b40;
  cursor: pointer;
  z-index: 2;
  background: rgba(34, 43, 64, .05);
  border: 1px solid #f3f3f3;
  border-radius: 23px;
  align-items: center;
  padding: 16px 24px;
  font-size: 14px;
  font-weight: 500;
  line-height: 100%;
  display: flex;
}

.__select__title:before, .__select__title:after {
  content: "";
  width: 10px;
  height: 2px;
  background-color: #91959f;
  transition: all .3s ease-out;
  display: block;
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translate(-3px, -50%)rotate(45deg);
}

.__select__title:after {
  transform: translate(3px, -50%)rotate(-45deg);
}

.__select__title:hover {
  border-color: #795bda;
}

.__select__title:hover:before, .__select__title:hover:after {
  background-color: #795bda;
}

.__select__content {
  width: calc(100% - 3px);
  z-index: 1;
  background-color: #f3f3f3;
  border: 1px solid rgba(199, 204, 209, 0);
  border-top: none;
  border-bottom-width: 0;
  border-radius: 10px;
  flex-direction: column;
  transition: all .3s ease-out;
  display: flex;
  position: absolute;
  top: 45px;
  left: 3px;
}

.__select__input {
  pointer-events: none;
  display: none;
}

.__select__input:checked + label:after {
  content: attr(data-value);
}

.__select__input:disabled + label {
  opacity: .6;
  pointer-events: none;
}

.__select__label {
  width: 100%;
  height: 46px;
  max-height: 0;
  cursor: pointer;
  align-items: center;
  padding: 0 16px;
  transition: all .2s ease-out;
  display: flex;
}

.__select__label + input + .__select__label {
  border-top: 0 solid rgba(199, 204, 209, .376);
}

.__select__label:before {
  content: attr(data-value);
  opacity: 0;
  transition: all .2s ease-out;
}

.__select__label:after {
  width: 200px;
  height: 40px;
  color: #333;
  z-index: -1;
  align-items: center;
  padding: 8px 16px;
  display: flex;
  position: absolute;
  top: -40px;
  left: 0;
}

