.articles {
    padding: 0 0 70px;
}

.articles__cards-wrap {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
}

.articles__card-column {
    flex: 1 1 360px;
    background: #fff;
    border-radius: 8px;
    transition: all 0.3s ease 0s;

    &:hover {
        box-shadow: 0 2 10 2 rgba(34, 43, 64, 0.1);
        transform: translateY(-3px);
    }
}

.card__block-top {
    display: flex;
    align-items: center;
    padding: 20px;

    img {
        width: 46px;
        margin-right: 10px;
    }
}

.card__name {
    h4 {
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;
        letter-spacing: -0.01em;
        color: #222B40;
        margin-bottom: 4px;
    }

    p {
        font-weight: 500;
        font-size: 12px;
        line-height: 120%;
        letter-spacing: -0.05em;
        color: rgba(34, 43, 64, 0.4);
    }
}

.card__block-title {
    a {
        display: block;
        text-decoration: none;
        font-weight: 500;
        font-size: 24px;
        line-height: 110%;
        letter-spacing: -0.06em;
        color: #222B40;
        padding: 20px;
    }

    p {
        font-weight: 500;
        font-size: 14px;
        line-height: 140%;
        // letter-spacing: -0.05em;
        color: #222B40;
        padding: 0 20px 20px;
    }
}

.card__block-statistic {
    padding: 0 20px 20px 20px;
    display: flex;
    justify-content: space-between;
}

.statistic_left {
    display: flex;
    align-items: center;

    .card__likes {
        margin-left: 20px;
    }
}

.card__date {
    text-align: right;

    span {
        font-weight: 500;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: -0.06em;
        text-transform: uppercase;
        color: rgba(34, 43, 64, 0.4);
    }
}

.articles__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: 14px;
    line-height: 100%;
    letter-spacing: -0.06em;
    color: #795BDA;
    border: none;
    background: rgba(121, 91, 218, 0.1);
    height: 46px;
    border-radius: 23px;
    padding: 16px 24px;
    margin: 30px auto 0;

    &:hover {
        background: rgba(121, 91, 218, 0.2);
    }
}

@media screen and (max-width: 1280px) {
    .articles__title {
        font-size: 42px;
        max-width: 258px;
    }

    .articles-section__icon-heart {
        position: absolute;
        bottom: 5px;
        right: 58px;
    }

    .articles__cards-wrap {
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 744px) {
    .articles-section__icon-heart {
        position: absolute;
        width: 10px;
        height: 8px;
        bottom: 4px;
        right: 50px;
    }

    .articles__cards-wrap {
        grid-template-columns: 1fr;
    }

    .articles__btn-390 {
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: -0.06em;
        color: #fff;
        border: none;
        background: #795bda;
        height: 46px;
        border-radius: 23px;
        padding: 16px 10px 16px 24px;
        margin: 20px 10px;
        width: 90%;

        &:hover {
            background: rgba(121, 91, 218, 0.2);
        }
    }

}

.filter{
    @media screen and (min-width: 744px) {
        padding: 40px 0 60px 0;
    }
}

.filter__body {
    align-items: flex-end;
}

//////////<h1 class="filter__title"></h1>
.filter__title{
    font-family: 'Golos Text', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 110%;
    letter-spacing: -0.06em;
    color: #222B40;
    @media screen and (min-width: 1280px) {
        font-size: 56px;
    }
}

.filter__inputs{
    @media screen and (min-width: 744px) {
        margin-top: 25px;
    }
    @media screen and (min-width: 1280px) {
        margin-top: 0;
    }
}

.input__search {
    @media screen and (min-width: 1280px) {
        width: 492px;;
    }
}

.filter-menu {
    @media screen and (min-width: 1280px) {
        width: 153px;
    }
}

.sort-menu {
    @media screen and (min-width: 1280px) {
        width: 153px;
    }
}

